	module.exports = {"data":{"site":{"siteMetadata":{"title":"MyBlog"}},"markdownRemark":{"html":"<p>In this post I will show how to make your personal blog website using Gatsby.</p>\n<p><a href=\"https://www.gatsbyjs.org\">Gatsby</a> is a static site generator with a lot of templates which you can use to build your project. One advantage of Gatsby is that it uses GraphQL, so you can get your content from multiple places starting from Markdown to Google Sheets! Ohh, did I tell that you build using React!! </p>\n<h2>Requirements</h2>\n<p>First, you have to install Gatsby command line interface tool. This tool is used to create new Gatsby projects. </p>\n<pre><code class=\"language-bash\">    npm install -g gatsby-cli\n</code></pre>\n<p>Next, initialize the blog with:</p>\n<pre><code class=\"language-bash\">    gatsby new my-blog https://github.com/mavamo135/my-blog\n</code></pre>\n<p> That's it! You can start your blog with:</p>\n<pre><code class=\"language-bash\">   cd my-blog\n   gatsby develop\n</code></pre>","frontmatter":{"title":"Making a blog with Gatsby","author":"Maximiliano Valencia","date":"12 March, 2018"}}},"pathContext":{"slug":"/posts/making-a-blog-with-gatsby/"}}

